body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --blue: #1e90ff;
  --white: #ffffff;
  --blueGrey: #f4f8fb;
  --grey: grey;
  --lightGrey: lightgrey;
  --darkBlue: blue;
  --lightBlue: #F4F4FA;
}

.app {
  text-align: center;
}

.imgSize {
  height: 2rem;
  width: 2rem;
}

.pointerCursor {
  cursor: pointer;
}

.navBar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #ffffff;
  background-color: var(--white);
  border-bottom: 1px solid #F4F4FA;
  border-bottom: 1px solid var(--lightBlue);
}

.navBarDisplay {
  display: flex;
  flex-direction: row;
  padding: 16px;
  justify-content: space-around;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
}

.blueBtn {
  padding: 10px 24px;
  background-color: #1e90ff;
  background-color: var(--blue);
  color: #ffffff;
  color: var(--white);
  font-size: 16px;
  border-radius: 6px;
  font-weight: 600;
  /* cursor: pointer; */
}

.fontFamily {
  font-family: 'Open Sans', sans-serif;
}

.font55px {
  font-size: 55px;
  font-weight: 600;
}

.font18px {
  font-size: 18px;
  font-weight: 500;
}

.container {
  padding-top: 40px;
  padding-bottom: 30px;
}

.intro {
  margin-left: auto;
  margin-right: auto;
}

.greyBg {
  background-color: #f4f8fb;
  background-color: var(--blueGrey);
  padding: 88px;
  margin: 48px 0px;
}

.txtGradient {
  background: -webkit-linear-gradient(var(--lightGrey), var(--darkBlue));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 550;
}

@media screen and (min-width: 480px) {
	h5 {
		font-size: 14px;
	}
}

.lineHeight {
  line-height: 54px;
}

.box {
  border-radius: 8px;
  box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.11);
  padding: 24px;
  border: 1px solid lightgrey;
  border: 1px solid var(--lightGrey);
  width: 30%;
}

.bold {
  font-family: sans-serif;
  font-weight: 600;
}
.form {
    /* width: 500px; */
}

.container {
    padding: 24px;
}

.fontFamily {
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
}

/* Popup style */
.popupBox {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
}

.box {
    position: relative;
    width: 50%;
    margin: 0 auto;
    height: auto;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background: #fff;
    border-radius: 4px;
    padding: 24px;
    border: 1px solid #999;
    overflow: auto;
}

.close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(26% - 30px);
    top: calc(100vh - 85vh - 33px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
}

.headingDecor {
    -webkit-text-decoration: dashed;
            text-decoration: dashed;
}

.inputWidth {
    width: 100%;
    font-family: 'Open Sans', sans-serif;
}

.subHeadingDecor {
    float: left;
}

.errorLine {
    color: red;
    float: left;
    font-size: 12px;
}

.blue {
    border: 0px;
    padding: 5px 10px;
    background-color: #1e90ff;
    color: #FFFFFF;
    font-size: 14px;
    border-radius: 4px;
    font-weight: 500;
    cursor: pointer;
}
